.control {
  position: relative;
  display: block;
  padding-left: 39px;
}

.control-mark {
  position: absolute;
  left: 0;
  background: #ffffff;
  border-radius: 4px;
  width: 20px;
  height: 20px;
}

.control-mark-checkbox:hover {
  background: rgba(255, 255, 255, 0.6);
}

.control-input[type="radio"]:checked:focus + .control-mark {
  outline: 3px solid #83b3d3;
}

.control-input[type="checkbox"]:focus + .control-mark {
  outline: 3px solid #83b3d3;
  background: rgba(255, 255, 255, 0.6);
}

.control-mark-checkbox:active {
  background: rgba(255, 255, 255, 0.3);
}

.control-input[type="checkbox"]:checked + .control-mark::after {
  position: absolute;
  content: url("../images/catalog/catalog-check.svg");
  display: block;
  top: -2px;
  left: 4px;
}

.control-lable {
  font-size: 18px;
  line-height: 23px;
}

.control-input[type="radio"] + .control-mark {
  border-radius: 50%;
}

.control-input[type="radio"]:checked + .control-mark::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #3f5e72;
  border-radius: 50%;
  top: 5px;
  left: 5px;
}

